import React from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components';

const antaList = {
  0 : {A : 'あんた！', B : 'あんた！！'},
  1 : {A : 'あんたほんと...', B : 'ってあんた！'},
  2 : {A : 'あんたあんたまじでさぁ', B : 'ってあんたさぁ！'},
  3 : {A : 'あんた', B : 'あんた'},
  4 : {A : 'あんた...', B : 'はやめときなさいよあんた'},
  5 : {A : 'あんたオタクほんと', B : 'なんだからあんた！'},
  6 : {A : 'あんた！！！！！！！！', B : 'ってちょっとあんた！！！！！！'},
  7 : {A : 'あんた！？', B : 'ってちょっとあんた！？！！？！'},
  8 : {A : 'ほんとあんたさぁ...' , B : 'はないわよあんた...'},
  9 : {A : 'お？喧嘩すっか？', B : 'でぃすこあがれよ！W'},
  10 : {A : 'あんたちょっとやめて！', B : 'あんた！'},
  11 : {A : 'あんた', B : 'やめろってまじでさぁ...'},
}

const BodyStyle = styled.div`
  text-align: center;
`;

const InputStyle = styled.div`
  border: solid 1px;
  margin-top: 1%;
  padding: 1px;
`;

class InputForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      antaText: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {

    if(this.state.value !== ''){
      const min = 0;
      const max = 12;
      const num = Math.floor( Math.random() * (max + 1 - min) ) + min ;
  
      this.setState({
        antaText : antaList[num].A + this.state.value + antaList[num].B,
      });
  
      event.preventDefault();
    }else{

      this.setState({
        antaText : 'あんた なんか入力しなさいよあんた！',
      });
  
      event.preventDefault();
    }
  }

  render() {
    return (
      <BodyStyle>
        {/* タイトル */}
        <div>
          <h1>あんたコンバータ</h1>
          <p>小花衣が好きすぎる<br />会話した気になれる</p>
        </div>

        {/* 入力 */}
        <InputStyle>
          <form onSubmit={this.handleSubmit}>
            <label>
              あんた なに！？ ≫　
              <input type="text" value={this.state.value} onChange={this.handleChange} />
            </label>
            　<input type="submit" value="あんた変換" />
          </form>
        </InputStyle>
        
        {/* 返却 */}
        <div>
          <h3>
          {this.state.antaText !== '' && (
            <p>「{this.state.antaText}」</p>
          )}
          </h3>
        </div>
      </BodyStyle>
    );
  }
}

// ========================================

ReactDOM.render(
  <InputForm />,
  document.getElementById('root')
);


